/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700|Alfa+Slab+One|Noto+Serif+KR:600");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer; }

@font-face {
  font-family: "Caponi Text";
  src: url("./CaponiText-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 400; }

@font-face {
  font-family: "Caponi Text";
  src: url("./CaponiText-Roman.woff") format("woff");
  font-style: normal;
  font-weight: 400; }

@font-face {
  font-family: "Caponi Text";
  src: url("./CaponiText-Semibold.woff") format("woff");
  font-style: normal;
  font-weight: 600; }

@font-face {
  font-family: "Caponi Display";
  src: url("./CaponiDisplay-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 400; }

@font-face {
  font-family: "Caponi Display";
  src: url("./CaponiDisplay-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700; }

body {
  background-color: #e7f7fb;
  font-family: 'Caponi Text', 'Noto Serif KR', sans-serif;
  font-size: 14px;
  line-height: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body, div, p {
  color: black; }

p, span, li {
  word-break: keep-all; }

body, div, p, ol, li {
  font-family: 'Caponi Text', 'Noto Serif KR', sans-serif;
  font-size: 1em;
  line-height: 1.3em;
  font-style: normal;
  font-weight: 400;
  color: black;
  text-rendering: optimizeLegibility; }

input {
  -webkit-box-shadow: none;
  box-shadow: none;
  outline: -webkit-focus-ring-color auto 0px;
  border: none; }

a {
  text-decoration: none;
  color: black;
  cursor: pointer; }

a:hover, a:focus {
  opacity: .5;
  -webkit-transition: all 0.1s ease-out;
  -moz-transition: all 0.1s ease-out;
  -ms-transition: all 0.1s ease-out;
  -o-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out; }

::-webkit-scrollbar {
  width: 5px;
  height: 5px; }

::-webkit-scrollbar-button {
  width: 10px;
  height: 10px; }

::-webkit-scrollbar-thumb {
  background: rgba(150, 150, 150, 0.5);
  border: 9px none #ffffff;
  border-radius: 50px; }

::-webkit-scrollbar-thumb:hover {
  background: rgba(150, 150, 150, 0.8); }

::-webkit-scrollbar-thumb:active {
  background: rgba(150, 150, 150, 0.8); }

::-webkit-scrollbar-track {
  background: #EFEFEF;
  border: 0px none #AAAAAA;
  border-radius: 36px; }

::-webkit-scrollbar-track:hover {
  background: #DFDFDF; }

::-webkit-scrollbar-track:active {
  background: #EEEEEE; }

::-webkit-scrollbar-corner {
  background: transparent; }

button {
  transition: 0.4s all; }

button:hover, button:focus {
  opacity: .5;
  -webkit-transition: all 0.1s ease-out;
  -moz-transition: all 0.1s ease-out;
  -ms-transition: all 0.1s ease-out;
  -o-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out; }

b {
  font-weight: 700;
  color: black; }

.fl {
  float: left; }

.marker {
  width: 10px;
  height: 10px;
  background-color: white;
  border-radius: 10px; }

.bold {
  font-size: 1.0em; }

.fr {
  float: right; }

.l-apple-box {
  height: 20px; }

.l-apple-box--half {
  height: 10px; }

.l-apple-box--double {
  height: 40px; }

br.clearing {
  clear: both !important; }

sup {
  font-size: 0.6em;
  position: relative;
  top: -0.6em; }

.map-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; }

.scroll-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 8; }

.section-container {
  width: 100%;
  position: relative; }

.non-sticky-box {
  position: absolute;
  left: 50%;
  -moz-transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
  -o-transform: translate(-50%, 0);
  -ms-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  top: 20px;
  width: 200px;
  padding: 20px;
  background: rgba(255, 0, 0, 0.5);
  color: black; }

.non-sticky-box--relative {
  position: relative;
  height: 90px; }
  .non-sticky-box--relative__box {
    position: absolute;
    left: 50%;
    -moz-transform: translate(-50%, 0);
    -webkit-transform: translate(-50%, 0);
    -o-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    top: 0;
    width: 200px;
    padding: 20px;
    background: rgba(255, 0, 0, 0.5);
    color: black;
    height: 50px; }

.sticky {
  position: sticky;
  top: 100px;
  height: 90px; }
  .sticky__box {
    position: absolute;
    left: 50%;
    -moz-transform: translate(-50%, 0);
    -webkit-transform: translate(-50%, 0);
    -o-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    top: 0;
    width: 200px;
    padding: 20px;
    background: rgba(255, 0, 0, 0.5);
    color: black;
    height: 50px; }

.center-marker {
  position: fixed;
  left: 50%;
  top: calc(50% - 50px);
  font-size: 1.5em;
  color: black;
  background-color: white;
  padding: 20px;
  border: 1px solid black;
  -moz-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  z-index: 5;
  font-weight: 600; }

.msg-contributor-info {
  z-index: 3;
  position: fixed;
  width: 650px;
  right: 30px;
  top: 50%;
  background-color: white;
  border: 1px solid black;
  padding: 20px;
  -moz-transform: translate(0, -50%);
  -webkit-transform: translate(0, -50%);
  -o-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%); }
  .msg-contributor-info .name-rank-area {
    display: flex; }
    .msg-contributor-info .name-rank-area .disclaimer {
      font-size: 0.7em;
      line-height: 1.4;
      color: #aaa;
      margin-top: 10px; }
    .msg-contributor-info .name-rank-area .lv {
      width: 50%;
      margin-bottom: 20px; }
      .msg-contributor-info .name-rank-area .lv .label {
        font-weight: bold;
        font-size: 0.9em;
        margin-bottom: 5px; }
      .msg-contributor-info .name-rank-area .lv .value {
        font-weight: bold;
        font-size: 1.1em; }
  .msg-contributor-info .changeset-area {
    border-top: 1px solid black;
    margin-top: 0;
    padding-top: 15px; }
    .msg-contributor-info .changeset-area .title-area {
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px; }
      .msg-contributor-info .changeset-area .title-area .label {
        font-weight: bold;
        font-size: 0.9em; }
      .msg-contributor-info .changeset-area .title-area .value {
        font-weight: bold;
        font-size: 0.9em; }

.year-legend {
  position: fixed;
  z-index: 3;
  bottom: 15px;
  right: 10px;
  border: 1px solid #ccc;
  padding: 10px;
  transition: 0.4s opacity;
  background-color: white; }
  .year-legend .title {
    font-family: 'Roboto', sans-serif;
    font-size: 0.7em;
    font-weight: 700;
    margin-bottom: 5px; }

.legend-sub-area {
  display: flex;
  margin-bottom: 2px; }
  .legend-sub-area .color-box {
    width: 12px;
    height: 12px;
    opacity: 0.7; }
  .legend-sub-area .value {
    margin-left: 10px;
    font-size: 0.7em;
    font-family: 'Roboto', sans-serif; }

.sub-legend {
  position: fixed;
  padding: 10px;
  background-color: white;
  right: 10px;
  z-index: 3;
  top: 10px;
  transition: 0.4s opacity; }
  .sub-legend .title {
    font-size: 0.7em;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    margin-bottom: 5px; }

.contributor-info {
  z-index: 3;
  position: fixed;
  background-color: white;
  border: 1px solid black;
  padding: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
  align-content: stretch; }
  .contributor-info.left {
    top: 20px;
    left: 20px; }
  .contributor-info.center {
    top: 20px;
    left: 50%;
    -moz-transform: translate(-50%, 0);
    -webkit-transform: translate(-50%, 0);
    -o-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    transform: translate(-50%, 0); }
  .contributor-info.right {
    top: 20px;
    right: 20px; }
